.intro {
  background-color: var(--color-black);
  text-align: center;
  position: fixed;
  z-index: 0;
  top: 88px;
  left: 0;
  padding-top: 100px;
  height: 700px;
  width: 100%;
  @include mobile {
    padding-top: 40px;
  }
}
.intro-logo {
  width: 352px;
  @include mobile {
    max-width: 352px;
    width: calc(96% - 48px);
  }
}
.intro-desc {
  margin: 30px;
  margin-bottom: 50px;
  color: var(--main-white);
  font-size: 22px;
  line-height: 27px;
  // @include mobile {

  // }
}
.intro-btn {
  width: 222px;
}
.intro-empty {
  height: 5px;
  margin-top: 1000px;
  background-color: transparent;
  @include mobile {
    margin-top: 700px;
  }
}
.intro-buffer {
  height: 160px;
}

.intro-vid {
  z-index: 5;
  text-align: center;
  margin: 0;
}

.i-video {
  width: 100vw;
  height: 100vh;
}

