#progress {
  position: absolute;
  left: 0;
  top: 150px;
  text-align: center;
  width: 100%;
  height: 100%;
  background: green;
  opacity: 0.5;
  margin-right: auto;
  margin-left: auto;
  transform-origin: 0 50%;
  animation: videowide auto linear;
  animation-timeline: scroll();
  // animation-timeline: videowide;
}

@keyframes videowide {
  0% {
    transform: scale(0.1);
  }
  20% {
    transform: scale(0.2);
    //transform: translateY(-100px);
  }
  50% {
    transform: scale(0.4);

    //transform: translateY(-100px);
  }
  80% {
    transform: scale(0.8);

    //transform: translateY(-100px);
  }
  100% {
    transform: scale(1);
    //transform: translateY(-100px);
  }
}

