  --main-beige:#FAF5F0;
  --main-bronze: #A3784E;
  --coffee-milk:#C0B3A8;
  --color-black: #0D0D0D;
  --dark-bronze: #886A44;
  --light-gray: #E6E6E6;
  --medium-gray: #B8B8B8;
  --dark-gray: #3d3d3d;
  --err-red: #FF3A2E;
  --bg-goods: #FAFAFA;
}

