  -webkit-transform: translate(-8px, 0);
  -ms-transform: translate(-8px, 0);
  transform: translate(-8px, 0);
  transition: 200ms;
  transition-timing-function: ease-in-out;
}

.the-arrow {
  width: 1px;
  transition: all 0.2s;
}
.the-arrow.-left {
  top: 5px;
  position: absolute;
}
.the-arrow.-left > .shaft {
  width: 1px;
  transition-delay: 0.2s;
}
.the-arrow.-left > .shaft:before, .the-arrow.-left > .shaft:after {
  width: 16px;
  transition-delay: 0.3s;
  transition: all 0.5s;
}
.the-arrow.-left > .shaft:before {
  transform: rotate(40deg);
}
.the-arrow.-left > .shaft:after {
  transform: rotate(-40deg);
}
.the-arrow > .shaft {
  display: block;
  height: 1px;
  background-color: var(--main-bronze);
  position: relative;
  transition: all 0.2s;
  transition-delay: 0;
  will-change: transform;
}
.the-arrow > .shaft:before, .the-arrow > .shaft:after {
  background-color: var(--main-bronze);
  display: block;
  height: 1px;
  position: absolute;
  top: 0;
  right: 0;
  transition: all 0.2s;
  transition-delay: 0;
}
.the-arrow > .shaft:before {
  transform-origin: top right;
}
.the-arrow > .shaft:after {
  transform-origin: bottom right;
}

.arr-left img {
  margin-right: 24px;
}

.arr-right img {
  margin-left: 24px;
}

.arr-left-on {
  text-align: right;
}

.arr-left-on, .arr-right-on {
  z-index: 10;
  vertical-align: middle;
}

.arr-left-on svg.icon path {
  transition: 0.2s;
}

.arr-left-on:hover svg.icon path {
  transition: 0.3s;
  d: path(" m0.2153,14.50535l-7.42364,-6.87472m-0.1,-0.1l7.52364,-7.02528m-7.51771,7.01993l32.05181,0");
}

.arr-right-on svg.icon path {
  transition: 0.2s;
}

.arr-right-on:hover svg.icon path {
  transition: 0.3s;
  d: path("m25.7154,14.47366l7.3846,-6.97472m0,0l-7.3846,-7.02528m7.41129,7.05197l-32.12669,-0.02669");
}

.icon-back-white svg.icon path {
